.button {
  border: 1px solid hsl(0 0 80);
  background-color: #eeeeee;
  padding: 6px 12px;
  border-radius: 3px;
  font-weight: 600;
  color: #000000;
  background-color: white;

  &:disabled {
    border: 1px solid #eeeeee;
    background-color: hsl(0 0 90);
    color: #888888;
  }

  &:not(:disabled):hover {
    background-color: hsl(0 0 90);
    border: 1px solid hsl(0 0 60);
  }
}